import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)
const Index= r => require.ensure([],()=>r(require('@/components/Index.vue')),'index')
const User = r=>require.ensure([],()=>(require('@/components/User.vue')),'user')
const Classify= r => require.ensure([],()=>r(require('@/components/Classify.vue')),'classify')
const Cart = r=>require.ensure([],()=>(require('@/components/Cart.vue')),'cart')
const Login= r=> require.ensure([],()=>(require('@/components/Login.vue')),'login')
const Footer= r=> require.ensure([],()=>(require('@/components/Footer.vue')),'footer')
const Search= r=> require.ensure([],()=>(require('@/components/Search.vue')),'search')
const Details= r=> require.ensure([],()=>(require('@/components/Details.vue')),'details')
const List= r=> require.ensure([],()=>(require('@/components/List.vue')),'list')
const Shangping= r=> require.ensure([],()=>(require('@/components/Shangping.vue')),'shangping')
const Xiangqing= r=> require.ensure([],()=>(require('@/components/Xiangqing.vue')),'xiangqing')

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    {path:'/',redirect:'/footer'},
    {
    	path:'/footer',
    	
    	component:Footer,
    	children:[
   				 {path:'/',redirect:'index'},

	    		{
			      path: 'index',
			      name: 'Index',
			      component: Index
			    },
			     {
			      path: 'classify',
			      name: 'Classify',
			      component: Classify,
			    },
			    {
			      path: 'user',
			      name: 'User',
			      component: User
			    },
			   
			    {
			      path: 'cart',
			      name: 'Cart',
			      component: Cart
			    },
			     {
					path:'list',
					name:'List',
					component:List
				}
	      
		    ]
    },
    
    
    {
    	path:'/login',
    	name:'Login',
    	component:Login
    },
    {
    	path:'/search',
    	name:'Search',
    	component:Search
    },
    {
    	path:'/details',
    	name:'Details',
    	component:Details,
    	children:[
    		{
    		path:'shangping',
    		name:'Shangping',
    		component:Shangping
    		},
    		{
    		path:'xiangqing',
    		name:'Xiangqing',
    		component:Xiangqing
    		}
    	]
    },
   
  ]
})



