import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

import {FETCHLIST,} from '../vuex/mutation-types';
import axios from 'axios';

var store=new Vuex.Store({
	strict:true,
	state:{
		count:2,
		list:[],
		items:[],
		classify:[],
		uu:[],
		xq:[]
	},
	// getters:{
	// 	lists(state){
	// 		return state.list
	// 	}
	// },
	mutations:{
		[FETCHLIST]:function(state,lists){
			this.state.list=lists.list
			this.state.items=lists.items
			this.state.classify=lists.classify
			this.state.uu=lists.uu
			this.state.xq=lists.xq
		}
	},
	actions:{
		
			fetchList({commit}){
				axios.get('/api/list').then((res)=>{

					commit(FETCHLIST,res.data)
					// console.log(res.data.classify.name)
					// console.log(commit)
				})
			
		}
	}
})
export default store


